<template>
  <div style="margin:0 auto;width:500px">
    <div style="padding:10px">{{playerColorName}}方-走棋</div>
    <table border="1" style="border:solid 1px">
      <template v-for="row in game.board.cells">
        <tr>
          <template v-for="cell in row">
            <td style="padding:5px;">
              <div @click="clickCell(cell)" :class="selectedCellClass(cell)">
                <cell v-bind="cell"></cell>
              </div>
            </td>
          </template>
        </tr>
      </template>
    </table>
  </div>
</template>
<script>
import { ajaxPost, ajaxGet } from "@/service/ajaxService.js";
import Cell from "@/views/Cell.vue";

export default {
  data() {
    return {
      roomNum: 1,
      currentPlayerColor: 0,
      game: {
        board: {
          cells: [[]]
        }
      },
      selectedCell: {}
    };
  },
  created() {
    this.startGame();
  },
  computed: {
    playerColorName() {
      return this.getPlayerColorName(this.currentPlayerColor);
    }
  },

  methods: {
    getPlayerColorName(color) {
      switch (color) {
        case 0:
          return "红";
        case 1:
          return "黑";
      }
      return "";
    },

    startGame() {
      ajaxPost("/game/start", { roomNum: this.roomNum })
        .then(rps => {
          this.game = rps.game;
          this.currentPlayerColor = 0;
        })
        .catch(err => {
          alert(err.message);
        });
        this.clearSelectedCell();
    },
    clickCell(cell) {
      if (this.selectedCell == cell) {
        this.clearSelectedCell();
      } else {
        if (this.selectedCell != null) {
          this.play(this.selectedCell, cell);
        } else {
          if (!cell.isFrontUp) {
            this.flip(cell);
          } else {
            this.selectedCell = cell;
          }
        }
      }
    },
    clearSelectedCell() {
      this.selectedCell = null;
    },
    selectedCellClass(cell) {
      if (cell == this.selectedCell) {
        return "selectedCell";
      } else {
        return "";
      }
    },
    nextColorTurn() {
      this.currentPlayerColor++;
      if (this.currentPlayerColor > 1) {
        this.currentPlayerColor = 0;
      }
    },
    flip(cell) {
      ajaxPost("/game/play", {
        roomNum: this.roomNum,
        playerColor: this.currentPlayerColor,
        fromX: cell.x,
        fromY: cell.y,
        toX: cell.x,
        toY: cell.y
      })
        .then(rps => {
          this.game = rps.game;
          this.nextColorTurn();
          this.clearSelectedCell();
        })
        .catch(err => {
          alert(err.message);
        });
    },
    play(fromCell, toCell) {
      ajaxPost("/game/play", {
        roomNum: 1,
        playerColor: this.currentPlayerColor,
        fromX: fromCell.x,
        fromY: fromCell.y,
        toX: toCell.x,
        toY: toCell.y
      })
        .then(rps => {
          console.log(rps);
          if (rps.gameOver) {
            alert(this.getPlayerColorName(rps.winnedPlayerColor) + "获胜");
            this.startGame();
          } else {
            this.game = rps.game;
            this.nextColorTurn();
            this.clearSelectedCell();
          }
        })
        .catch(err => {
          alert(err.message);
        });
        
    }
  },
  components: {
    Cell
  }
};
</script>
<style scoped>
.selectedCell {
  background-color: #eeeeee;
}
</style>