<template>
  <div
    style="width:40px;height:40px;vertical-align:middle;line-height:40px;border:solid;border-radius:60px;"
    :style="{color:borderColor}"
  >{{title}}</div>
</template>
<script>
export default {
  name: "cell",
  props: {
    piece: {
      type: Object,
      default: () => ({
        name: "",
        color: "gray",
        value: -1
      })
    },
    x: {
      type: Number
    },
    y: {
      type: Number
    },
    isFrontUp: {
      type: Boolean
    }
  },
  data() {
    return {};
  },
  computed: {
    title() {
      if (this.isFrontUp == false) {
        return "";
      } else {
        if (this.piece == null) {
          return "";
        } else {
          return this.piece.name;
        }
      }
    },
    borderColor() {
      if (this.isFrontUp == false) {
        return "gray";
      } else {
        if (this.piece != null) {
          if (this.piece.color == 0) {
            return "red";
          } else {
            return "black";
          }
        } else {
          return "white";
        }
      }
    }
  },
  watch: {},
  created() {
    console.log(this.piece);
    console.log(this.x);
    console.log(this.y);
    console.log(this.isFrontUp);
  }
};
</script>
