import axios from 'axios';

const config = {
    baseURL: '',
    timeout: 30 * 1000,
    headers : {
        'Content-Type':'application/json;charset=UTF-8'
    },
};
const _axios = axios.create(config);

function doGet(url, params, successFun, failedFun, isFreshToken) {
    if(url.indexOf("?") != -1){
        url = url + "&n="+encodeURIComponent(Math.random())
    }else{
        url = url + "?n="+encodeURIComponent(Math.random())
    }
    _axios({ method: 'get', url, params })
        .then(function (response) {
            var res = response.data;
            if (res.success) {
                successFun(res);
            }
            else {
                    alert(res.message);
                    console.log(response);
                }
        })
        .catch(function (error) {
            alert(error);
        })
}

function doPost(url, params, successFun, failedFun, isFreshToken) {
   _axios.post(url, params)
        .then(function (response) {
            var res = response.data;
            if (res.success) {
                successFun(res);
            }
            else {
               
                    if (failedFun == null || failedFun == undefined)
                    {
                        alert(res.message);
                    }
                    else
                    {
                        failedFun(res);
                    }
                    console.log(response);
                }
            
        })
        .catch(function (error) {
            alert(error);
        })
}

export function ajaxGet(url, params) {
    return new Promise(function (s, f) {
        doGet(url, params, s, f, false);
    });
}

export function ajaxPost(url, params) {
    return new Promise(function (s, f) {
        doPost(url, params, s, f, false);
    });
}